.categoriesMenu {
  display: flex;
  justify-content: center;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid #0c223b;
  padding: 0;
  width: fit-content;
  margin: 25px auto;
  .categoryActive {
    background: transparent;
    color: #fff;
  }
  li {
    cursor: pointer;
    list-style: none;
    margin: 0;
    padding: 25px 35px;
    background-color: #0c223b;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-weight: bold;
    text-align: center;
    &:first-child {
      border-radius: 8px 0 0 8px;
    }
    &:last-child {
      border-radius: 0 8px 8px 0;
    }
  }
}
