.nav {
  background-color: #0c223b;
  .appbar {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    background-color: #0c223b;
    .navLeft {
      display: flex;
      justify-content: center;
      .appbarBrandLogo {
        width: 25px;
      }
      .appbarBrandText {
        font-size: 16px;
        color: #fff;
        font-family: "aileron-heavy";
        margin: auto 15px;
        text-decoration: none;
        font-weight: bold;
      }
    }
    .navRight {
      display: flex;
      justify-content: center;
      .appbarPseudo {
        text-decoration: none;
        color: #fff;
        margin: auto 15px;
      }
      .appbarMenu {
        font-size: 28px;
        color: #fff;
        text-decoration: none;
        font-weight: bold;
        img {
          width: 100%;
        }
      }
    }
  }
}
.pageContent {
  background-image: url("../images/background.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
