@font-face {
  font-family: "aileron-black";
  src: url("./fonts/Aileron-Black.otf");
}

* {
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #fff;
  box-shadow: inset 0 0 5px #0c223b;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #0c223b80;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #0c223b;
}

body {
  margin: 0;
  overflow-x: hidden;
}

.film-block-title {
  text-align: center;
  font-weight: bold;
}

.home-screen {
  padding: 15px 0;
  display: flex;
  flex-direction: column;
  .header {
    padding: 35px 35px;
    display: flex;
    justify-content: space-between;
    .header-left {
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        margin-right: 25px;
        font-size: 24px;
        color: #fff;
      }
      select {
        height: 5vh;
        width: 15vw;
      }
    }
    .header-right {
      padding: 15px 35px;
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        margin-right: 25px;
        font-size: 24px;
        color: #fff;
      }
      form {
        display: flex;
        justify-content: center;
        height: 5vh;
        input {
          height: 100%;
          width: 15vw;
        }
        button {
          height: 100%;
          display: flex;
          justify-content: center;
          font-size: 24px;
          border: none;
          border-radius: 0 10px 10px 0;
          align-items: center;
          background-color: #0c223b;
          text-decoration: none;
          color: #fff;
        }
      }
    }
  }
  .result-feedback {
    padding: 10px;
    color: #008000;
    background: #0080003d;
    border-radius: 5px;
    margin: 25px 75px;
  }
}

.filmsBlock {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .film-block_title {
    font-size: 36px;
    font-family: "aileron-black";
    color: #fff;
  }
  .film-block_films {
    padding: 10px;
    display: flex;
    flex-direction: wrap;
  }
  .film-block_links {
    display: flex;
    justify-content: flex-end;
    align-self: flex-end;
    padding: 0;
    border-radius: 5px;
    margin: 25px;
    .links-more {
      display: flex;
      justify-content: center;
      align-items: center;
      list-style: none;
      text-decoration: none;
      cursor: pointer;
      color: #0c223b;
      width: 40px;
      height: 40px;
      border-radius: 100%;
      border: 2px solid #0c223b;
      background-color: #fff;
      margin: 0 25px;
      i {
        font-size: 25px;
        font-weight: bold;
      }
      &:hover {
        background: #0c223b;
        color: #fff;
        // &:first-child {
        //   &::after {
        //     content: "prev";
        //     color: #0C223B;
        //     font-size: 18px;
        //   }
        // }
        // &::after {
        //   content: "next";
        //   color: #0C223B;
        //   font-size: 18px;
        // }
      }
    }
  }
}

.films-block {
  padding: 15px 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  .films-container__card {
    background-color: #ffffff4d;
    width: 25%;
    border-radius: 10px;
    box-shadow: 5px;
    display: flex;
    box-shadow: 3px;
    flex-direction: column;
    align-items: center;
    margin: 25px;
    box-shadow: 0px 0px 15px #0c223b;
    overflow: hidden;
    padding-bottom: 0;
    position: relative;
    .card-title {
      padding: 15px;
      border-bottom: 1px solid #0c223b;
      color: #fff;
      font-weight: bold;
      font-size: 16px;
      width: 100%;
      margin: 0;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .card-media {
      padding: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 5px;
      margin: 7px 0;
      margin-bottom: 50px;
      position: relative;
      overflow: hidden;
      img {
        width: 100%;
        border: 15px solid #0c223b86;
        border-radius: 10px;
      }
      .card-announce {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        transition: 0.3s all;
        cursor: pointer;
        span {
          display: inline-block;
          position: absolute;
          right: -100%;
          background-color: #0c223bc4;
          border-radius: 10px 0 0 10px;
          padding: 15px;
          font-size: 14px;
          color: #fff;
          font-weight: 900;
          height: min-content;
          font-family: "aileron-black";
          top: 70%;
          right: -10000px;
          bottom: 0;
        }
        &:hover {
          span {
            right: 0;
          }
        }
      }
    }
    .card-footer {
      display: flex;
      justify-content: space-between;
      border-top: 1px solid #0c223b;
      padding: 10px;
      width: 100%;
      margin: 0;
      position: absolute;
      margin-top: 15px;
      bottom: 0;
      a {
        text-decoration: none;
        font-size: 16px;
        color: #fff;
        font-weight: bold;
        cursor: pointer;
      }
      i {
        color: #fff;
        font-weight: bold;
        font-size: 20px;
        text-decoration: none;
      }
    }
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #0c223b80;
  display: flex;
  justify-content: center;
  align-items: center;
  .modal-detail {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url("./components/images/background.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 0 0 50px #0c223b;
    padding: 0;
    width: 50%;
    height: 70%;
    border-radius: 25px;
    .modal-header {
      display: flex;
      width: 100%;
      padding: 5px auto;
      justify-content: center;
      align-items: center;
      text-align: center;
      color: #fff;
      font-size: 20px;
      font-weight: 900;
      border-bottom: 1px solid #fff;
    }
    .modal-img {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 15px auto;
      img {
        width: 50%;
        border-radius: 100%;
      }
    }
    .modal-body {
      width: 90%;
      overflow-y: scroll;
      flex-grow: 1;
      p {
        font-size: 18px;
        font-weight: 200;
        color: #fff;
        text-align: left;
        .def-list {
          margin-left: 15%;
        }
        span {
          font-weight: bold;
          color: #fff;
        }
      }
    }
    .btn-close {
      text-align: right;
      padding: 5px 15px;
      border-radius: 5px;
      color: #800000;
      border: 2px solid #800000;
      background-color: #fff;
      font-size: 20px;
      text-decoration: none;
      margin: 5px 0 15px;
      cursor: pointer;
      &:hover {
        color: #fff;
        background-color: #800000;
      }
    }
    .modal-footer {
      display: flex;
      justify-content: center;
      width: 100%;
      padding: 5px auto;
      align-items: center;
      text-align: center;
      color: #fff;
      font-size: 20px;
      font-weight: 900;
      border-top: 1px solid #fff;
    }
  }
}

.favorite-film-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  .favorite-title {
    align-self: flex-start;
    position: relative;
    left: 25px;
    font-size: 36px;
    font-family: "aileron-black";
    color: #fff;
  }
  .btn-delete {
    text-align: center;
    text-decoration: none;
    padding: 10px 15px;
    color: #800000;
    cursor: pointer;
    background-color: #fff;
    border: 2px solid #800000;
    border-radius: 5px;
    margin-right: 0;
    text-align: right;
    width: fit-content;
    margin: 30px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
      background-color: #800000;
      color: #fff;
    }
  }
}

.history-screen {
  padding: 25px;
  .page-title {
    position: relative;
    left: 25px;
    font-size: 36px;
    font-family: "aileron-black";
    color: #fff;
    margin: 0;
  }
  .history-container {
    padding: 25px;
  }
  .btn-delete {
    text-align: center;
    text-decoration: none;
    padding: 10px 15px;
    color: #800000;
    cursor: pointer;
    background-color: #fff;
    border: 2px solid #800000;
    border-radius: 5px;
    margin-right: 0;
    text-align: right;
    width: fit-content;
    margin: 30px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
      background-color: #800000;
      color: #fff;
    }
  }
}
.history-card {
  padding: 0.5%;
  width: 90%;
  box-shadow: 5px;
  border-radius: 10px;
  background-color: #ffffff4d;
  box-shadow: 0px 0px 15px #0c223b;
  margin: 25px auto;
  padding: 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .card-start {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 30%;
    input {
      margin-right: 25px;
    }
    p {
      width: max-content;
      color: #0c223b;
      span {
        color: #0c223b;
        font-weight: 900;
      }
    }
  }
  .card-body {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 14px;
    width: 40%;
    p {
      color: #0c223b;
      width: max-content;
      text-align: center;
      span {
        color: #0c223b;
        font-weight: 900;
      }
    }
  }
  .card-action {
    width: 30%;
    display: flex;
    justify-content: flex-end;
    margin: 0 15px;
    span {
      cursor: pointer;
      height: min-content;
      padding: 5px 10px;
      margin: 0 10px;
      border: 2px solid #0c223b;
      color: #0c223b;
      background-color: #fff;
      border-radius: 5px;
      margin: 0 10px;
      &:last-child {
        margin: 0;
      }
      &:hover {
        background-color: #0c223b;
        color: #fff;
      }
      &:last-child {
        border: 2px solid #800000;
        color: #800000;
        background-color: #fff;
        &:hover {
          background-color: #800000;
          color: #fff;
        }
      }
    }
  }
  .more-block-btn {
    border: 2px solid #0c223b;
    border-radius: 3px;
    color: #0c223b;
    padding: 5px 15px;
    color: #fff;
  }
}

.footer {
  background-image: url(./components/images/harry.jpg);
  background-size: cover;
  // background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: bottom;
  margin-bottom: 0;
  & > p {
    text-align: center;
    font-size: 14px;
    color: #fff;
    margin: 0;
    font-weight: bold;
    padding-bottom: 25px;
    background-color: #0c223b8c;
  }
  .footer-overlay {
    background-color: #0c223b8c;
    display: flex;
    justify-content: space-around;
    padding: 50px 30px;
    .footer__block {
      width: 30%;
      h2 {
        color: #fff;
        font-family: "aileron-black";
      }
      p {
        color: #fff;
        font-size: 20px;
        a {
          text-decoration: none;
          color: #fff;
          font-style: normal;
          font-weight: bold;
          font-size: 16px;
        }
      }
      &:last-child {
        p {
          margin: 7px 0;
        }
      }
    }
  }
}
