@font-face {
  font-family: "aileron-sb";
  src: url("../../fonts/Aileron-SemiBold.otf");
}

@font-face {
  font-family: "aileron-thin";
  src: url("../../fonts/Aileron-Thin.otf");
}

@font-face {
  font-family: "aileron-light";
  src: url("../../fonts/Aileron-Light.otf");
}

@font-face {
  font-family: "aileron-italic";
  src: url("../../fonts/Aileron-BoldItalic.otf");
}

@font-face {
  font-family: "aileron-reg";
  src: url("../../fonts/Aileron-Regular.otf");
}

* {
  box-sizing: border-box;
  font-family: "aileron-light";
}
.splashContainer {
  background-image: url("../images/background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  .splash {
    width: 100%;
    background-color: #18447763;
    padding: 1% 7%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-content: center;
    .splash__header {
      font-family: "aileron-sb";
      font-size: 35px;
      text-align: center;
      color: #fff;
      margin: 10px auto;
      .subHead {
        font-family: "aileron-thin";
        font-weight: 100;
      }
      .splashSpinner {
        text-align: center;
        display: flex;
        justify-content: center;
        img {
          width: 3vw;
          border-radius: 5px;
        }
      }
    }
    .splash__body {
      border-radius: 20px;
      padding: 1%;
      background-color: #ffffff4d;
      display: flex;
      flex-direction: column;
      align-content: center;
      margin: auto;
      width: 60%;
      .splashHero {
        color: #fff;
        font-family: "aileron-thin" !important;
        font-size: 25px;
        text-align: center;
        padding: 50px auto;
      }
      .splashSpinner {
        text-align: center;
        display: flex;
        justify-content: center;

        margin-bottom: 15px;
        img {
          width: 5vw;
          border-radius: 5px;
        }
      }
    }
    .splash__footer {
      border-top: 1px solid #fff;
      padding: 25px 10vw;
      text-align: center;
      font-size: 14px;
      color: #fff;
      font-family: "aileron-reg";
    }
  }
}
