.contextModal {
  transition: 1s all;
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 2;
  padding: 0;
  background-color: #000000b4;
  border-radius: 5px;
  overflow: hidden;
  li {
    transition: 0.3s color;
    list-style: none;
    padding: 20px 30px;
    cursor: pointer;
    color: #ffffff7e;
    font-weight: bold;
    &:hover {
      background-color: #000000;
      color: #fff;
    }
  }
}
.modalHidden {
  display: none;
  transition: 0.3s all;
  position: absolute;
  top: 10px;
  right: -200px;
  padding: 0;
  background-color: #000000b4;
  border-radius: 5px;
  overflow: hidden;
  li {
    transition: 1s color;
    cursor: pointer;
    list-style: none;
    padding: 20px 30px;
    color: #ffffff7e;
    font-weight: bold;
    &:hover {
      background-color: #000000;
      color: #fff;
    }
  }
}
