@font-face {
  font-family: "aileron-black";
  src: url("../../fonts/Aileron-Black.otf");
}
@font-face {
  font-family: "aileron-light";
  src: url("../../fonts/Aileron-Light.otf");
}

.hero {
  background-image: url("../images/Hero.jpg");
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  padding: 50px 50px;
  .hero__text {
    width: 90%;
    color: #fff;
    font-size: 42px;
    position: relative;
    left: 45px;
    top: 45px;
    font-family: "aileron-black";
    .hero__textFooter {
      font-size: 24px;
      font-size: "aileron-light";
      font-weight: 200;
      color: #fff;
      margin-top: 0;
    }
  }
  .hero__action {
    display: flex;
    a {
      text-decoration: none;
      color: #ffffffb7;
      background-color: #0c223b;
      border: 3px solid #0c223b;
      padding: 15px 30px;
      font-size: 24px;
      border-radius: 15px;
      font-weight: bold;
      margin: 25px 15px;
      &:hover {
        transition: 0.5s color;
        background-color: #0c223ba6;
        color: #fff;
      }
    }
  }
}

.secondHero {
  height: 300px;
  background-image: url(../images/harry.jpg);
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
