.connectionScreen {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px 35px;
  .connectionHero {
    h1 {
      align-self: flex-start;
      position: relative;
      left: 25px;
      font-size: 36px;
      font-family: "aileron-black";
      color: #fff;
      margin-bottom: 35px;
    }
    p {
      margin: 5vw;
      text-align: justify;
      font-size: 24px;
      font-weight: 200px;
      color: #fff;
    }
  }
  form {
    width: 70vw;
    display: flex;
    flex-direction: column;
    p {
      width: 100%;
      color: #fff;
      margin: 5px auto;
      input,
      select {
        border: none;
        color: #fff;
        height: 35px;
        width: 100%;
        margin: 0;
        background: transparent;
        border-bottom: 2px solid #0c223b;
      }
      label {
        margin: 0;
        font-size: 20px;
        color: #fff;
      }
    }
    .btnSubmit {
      text-align: right;
      text-decoration: none;
      width: min-content;
      display: inline-block;
      margin: 25px 0;
      font-weight: bold;
      padding: 15px 25px;
      background: transparent;
      color: #fff;
      border-radius: 3px;
      border: 2px solid #0c223b;
      align-self: flex-end;
      font-size: 20px;
      cursor: pointer;
      &:hover {
        transition: 0.5s all;
        background-color: #0c223b;
        color: #fff;
      }
    }
  }
}
