.navigation {
  background-color: #0c223b;
  padding: 5px 25px;
  display: flex;
  justify-content: center;
  .navTabs {
    // border-bottom: 1px solid #fff;
    display: flex;
    justify-content: center;
    list-style: none;
    padding-right: 35px;
    .menuTab {
      text-align: center;
      width: 15vw;
      color: #fff;
      font-weight: bold;
      font-size: 18px;
      margin: auto 15px;
      cursor: pointer;
      a {
        text-decoration: none;
        color: #fff;
        display: inline-block;
        padding-bottom: 15px;
      }
      i {
        font-size: 14px;
      }
      a:active {
        color: #fff;
      }
    }
    .borderBottom {
      text-align: center;
      width: 15vw;
      color: #fff;
      font-weight: bold;
      font-size: 18px;
      margin: auto 15px;
      cursor: pointer;
      border-bottom: 3px solid #fff;
      a {
        text-decoration: none;
        color: #fff;
        display: inline-block;
        padding-bottom: 15px;
      }
      i {
        font-size: 14px;
      }
      a:active {
        color: #fff;
      }
    }
  }
}
