* {
  box-sizing: border-box;
}

.trending {
  padding: 10px 30px;
  background-color: #fff;
  margin: 0;
  .trendingHead {
    display: flex;
    justify-content: space-between;
    margin: 10px auto;
    span {
      border-radius: 5px;
      padding: 5px 10px;
      background-color: #ffd000;
      color: #0c223b;
      font-weight: 900px;
      &:last-child {
        border: 2px solid #0c223b;
        color: #0c223b;
        background-color: #fff;
        cursor: pointer;
        font-weight: bold;
        transition: 0.3s color;
        &:hover {
          background-color: #0c223b;
          color: #fff;
        }
      }
    }
  }
  .trendingBody {
    padding: 5px;
    width: 100%;
    display: flex;
    overflow-x: scroll;
    .trendingMovieCard {
      box-shadow: 0px 0px 10px #0c223b;
      border-radius: 15px;
      overflow: hidden;
      position: relative;
      margin: 10px;
      display: flex;
      flex-direction: column;
      min-width: 15vw;
      align-items: center;
      padding: 0;
      .cardBackMedia {
        width: 100%;
        height: 150px;
        border-radius: 15px 15px 0px 0px;
      }
      .cardPostMedia {
        position: absolute;
        top: 45%;
        width: 70px;
        height: 70px;
        border-radius: 50%;
        margin-bottom: 15px;
      }
      .cardFoot {
        height: 100px;
        width: 100%;
        background-color: #0c223b;
        border-radius: 0px 0px 15px 15px;
        // .footTitle {
        //   margin: 0;
        //   color: #fff;
        //   text-align: center;
        // }
        .footInfo {
          margin: 0;
          padding: 50px 3px 10px;
          display: flex;
          justify-content: center;
          span {
            color: #fff;
            font-size: 14px;
            font-weight: bold;
          }
        }
      }
    }
  }
}
